/* eslint-disable no-undef */
import React, { Component } from 'react'
import { compose } from 'recompose'

import Button from '@material-ui/core/Button'
import { withStyles } from '@material-ui/core/styles'
import sweetConnect from 'redux/sweet-connect'
import { isLoggingInSelector, isLoggedInSelector } from 'redux/selectors'

class GoogleSignIn extends Component {
	constructor(props) {
		super(props)
		this.googleSignInButton = React.createRef()
	}

	handleGoogleLogin = googleUser => {
		console.log('googleUser', googleUser)
	}

	componentDidMount() {
		gapi.load('auth2', () => {
			// Retrieve the singleton for the GoogleAuth library and set up the client.
			this.auth2 = gapi.auth2.init({
				client_id: process.env.GOOGLE_SIGN_IN_CLIENT_ID,
				cookiepolicy: 'single_host_origin',
				// Request scopes in addition to 'profile' and 'email'
				//scope: 'additional_scope'
			})
			// attachSignin(document.getElementById('customBtn'))
			this.auth2.attachClickHandler(
				this.googleSignInButton,
				{},
				this.handleGoogleLogin
			)
		})
	}

	render() {
		return (
			<Button
				ref={this.googleSignInButton}
				size="large"
			>
				<img src="/images/google.png" height="32" width="32" alt="Google Login" />
				&nbsp;&nbsp;
				{'Login with Google'}
			</Button>
		)
	}
}

const styles = {
	googleLogin: {
		// todo
	},
}

export default compose(
	withStyles(styles),
	sweetConnect({
		selectors: {
			isLoggingIn: isLoggingInSelector,
			isLoggedIn: isLoggedInSelector,
		},
	})
)(GoogleSignIn)


